.home {
    width: 80%;
    height: 82vh;
    float: right;
    position: relative;
  }
  .home-aboutMe{
    font-size: 2vw;
    color: lightcoral;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
    height: 40vh;
    width: 78vw ;
    padding-left: 5vw;
  }
  .home-switch-title{
    display: inline-block;
    font-size: 2vw;
    color: lightcoral;
    height: 2vw;
    position: relative;
    top: 0.5vw;
    line-height: 2vw;
    padding-left: 1vw;
  }
  .home-aboutMe-animation{
    position: relative;
    width: 5vw;
    height: 5vw;
    animation:aboutMeEnter 3s;
  }
  .home-carousel{
    animation:carouselMeEnter 3s;
    width: 35vw;
    height: 25vw;
    position: absolute;
    top: 40vh;
    right: 5vw;
  }
  .home-skill-item{
    width: 30vw;
    height: 2vh;
    border-radius: 2vw;
    border: 1px solid red;
  }
  .home-skill{
    width: 32vw;
    margin-top: 2vh;
  }
  .home-skill:nth-child(1) .home-skill-item-bgc{
    animation: skillWidth1 3s ease 3s;
    animation-fill-mode: forwards
  }
  .home-skill:nth-child(2) .home-skill-item-bgc{
    animation: skillWidth1 3s ease 3s;
    animation-fill-mode: forwards
  }
  .home-skill:nth-child(3) .home-skill-item-bgc{
    animation: skillWidth1 3s ease 3s;
    animation-fill-mode: forwards
  }
  .home-skill:nth-child(4) .home-skill-item-bgc{
    animation: skillWidth2 3s ease 3s;
    animation-fill-mode: forwards
  }
  .home-skill:nth-child(5) .home-skill-item-bgc{
    animation: skillWidth2 3s ease 3s;
    animation-fill-mode: forwards
  }
  .home-skill:nth-child(6) .home-skill-item-bgc{
    animation: skillWidth3 3s ease 3s;
    animation-fill-mode: forwards
  }
  .home-skill:nth-child(7) .home-skill-item-bgc{
    animation: skillWidth3 3s ease 3s;
    animation-fill-mode: forwards
  }
  .home-skill:nth-child(8) .home-skill-item-bgc{
    animation: skillWidth4 3s ease 3s;
    animation-fill-mode: forwards
  }
  .home-skill:nth-child(9) .home-skill-item-bgc{
    animation: skillWidth4 3s ease 3s;
    animation-fill-mode: forwards
  }
  .home-skill-item-bgc{
    height: 2vh;
    border-radius: 2vw;
  }
  .home-study{
    font-size: 2vw;
    color: lightcoral;
  }
  @keyframes aboutMeEnter
{
from{
  transform: rotate(0deg);
  left: 100vw;
}
to{
  transform: rotate(2160deg);
  left: 0vw;
}
}
@keyframes carouselMeEnter
{
from{
  opacity: 0;
}
to{
  opacity: 1;
}
}
@keyframes  skillWidth1
{
from{
width: 0vw;
background-color: rgba(255, 0, 0, 0);
}
to{
  width: 25vw;
  background-color: rgb(57, 53, 182);
}
}
@keyframes  skillWidth2
{
from{
width: 0vw;
background-color: rgba(0, 0, 0, 0);
}
to{
  width: 16vw;
  background-color: rgb(230, 42, 42);
}
}
@keyframes  skillWidth3
{
from{
width: 0vw;
background-color: rgba(0, 0, 0, 0);
}
to{
  width: 19vw;
  background-color: rgb(227, 236, 52);
}
}
@keyframes  skillWidth4
{
from{
width: 0vw;
background-color: rgba(0, 0, 0, 0);
}
to{
  width: 10vw;
  background-color: rgb(53, 220, 72);
}
}
@media screen and (max-width:1000px){
  .home {
    float: none;
    width: 100vw;
    position: relative;
    padding-left: 5vw;
  }
  .home-carousel{
    width: 50vw;
    height: 40vw;
    top: 58vh;
  }
  .home-switch-title{
    font-size: 4vw;
  }
  .home-aboutMe{
    font-size: 2vw;
    width: 40vh;
    position: relative;
    left: 10vw;
  }
}