 .carousel {
   /* 子绝父相 */
  position: relative;
    width: 35vw;
    height: 25vw;
    overflow: hidden;
    display: flex;
    flex-wrap: nowrap;
  }
  .carousel-img-item {
    width: 35vw;
    transition: all 2s;
  }
  .carousel-img-item img {
    width: 35vw;
    height: 25vw;
  }
  .carousel-point {
    position: absolute;
    bottom: 0;
    right: 0;
  }
  .carousel-point-item{
    display: inline-block;
    width: 1vw;
    height: 1vw;
    border: 1px solid rgb(23, 91, 208);
    border-radius: 50%;
    margin-right: 0.5vw;
  }
  @media screen and (max-width:1000px){
    .carousel {
      right: 22vw;
      width: 50vw;
      height: 40vw;
      overflow: hidden;
      display: flex;
      flex-wrap: nowrap;
    }
    .carousel-img-item {
      width: 50vw;
    }
    .carousel-img-item {
      width: 50vw;
      height: 40vw;
      transition: all 2s;
    }
    .carousel-img-item img {
      width: 50vw;
      height: 40vw;
    }
    .carousel-point-item{
      display: inline-block;
      width: 2.5vw;
      height: 2.5vw;
      border: 1px solid rgb(23, 91, 208);
      border-radius: 50%;
      margin-right: 0.5vw;
    }
  }
  